import React, { useState } from 'react';

import Draggable from 'react-draggable';
import ComponentFactory from './ComponentFactory';
import styled from 'styled-components';
import './PreviewSection.css';
import { collection, getDocs, query } from 'firebase/firestore';
import { db } from '..';

const PreviewSection = ({ themeSettings }) => {
  const { textColor, fontSize, width, fontFamily } = themeSettings;
  const [droppedComponents, setDroppedComponents] = useState([]);
  // const [selectedComponent, setSelectedComponent] = useState(null);
  // const [textColor, setTextColor] = useState('black');
  // const [fontSize, setFontSize] = useState('36px');
  // const [width, setWidth] = useState('100%');
  // const [fontFamily, setFontFamily] = useState('Arial');
  // const getThemes = async () => {
  //   const q = query(collection(db, 'Themes'));
  //   const querySnapshot = await getDocs(q);
  //   querySnapshot.forEach((doc) => {
  //     const themeData = doc.data();
  //     console.log(themeData.textColor);
  //     setFontSize(themeData.fontSize);
  //     setTextColor(themeData.textColor);
  //     setWidth(themeData.width);
  //     setFontFamily(themeData.fontFamily);
  //   });
  // };
  // getThemes();
  const Preview = styled.div`
    width: ${width};
    color: ${textColor};
    font-size: ${fontSize};
    font-family: ${fontFamily};
  `;
  const handleDrop = (e) => {
    e.preventDefault();
    const componentType = e.dataTransfer.getData('text/plain');
    const newComponent = ComponentFactory.createComponent(componentType);
    setDroppedComponents([...droppedComponents, newComponent]);
  };
  // const handleSelectComponent = (componentType) => {
  //   setSelectedComponent(componentType);
  // };
  const handleDragOver = (e) => {
    e.preventDefault();
  };
  return (
    <div className="preview-container">
      <h2>Preview Section</h2>
      <Preview
        className="preview-section"
        onDrop={handleDrop}
        onDragOver={handleDragOver}
      >
        {droppedComponents.map((component, index) => (
          <Draggable key={index}>
            <div className="dropped-component">{component}</div>
          </Draggable>
        ))}
      </Preview>
    </div>
  );
};
export default PreviewSection;
