// PropertiesSection.js
import React, { useState } from 'react';
import './PropertiesSection.css';
import { db } from '..';
import { addDoc, collection } from 'firebase/firestore';

const PropertiesSection = ({ themeSettings, setThemeSettings }) => {
  const { textColor, fontSize, width, fontFamily } = themeSettings;

  const handleTextColorChange = (e) =>
    setThemeSettings({ ...themeSettings, textColor: e.target.value });
  const handleFontSizeChange = (e) =>
    setThemeSettings({ ...themeSettings, fontSize: e.target.value + 'px' });
  const handleWidthChange = (e) =>
    setThemeSettings({ ...themeSettings, width: e.target.value });
  const handleFontFamilyChange = (e) =>
    setThemeSettings({ ...themeSettings, fontFamily: e.target.value });

  const saveSettingsToDatabase = async (e) => {
    e.preventDefault();
    const theme = { textColor, fontSize, width, fontFamily };
    await addDoc(collection(db, 'Themes'), theme);
  };
  return (
    <div className="properties-section">
      <h2>Properties Section</h2>
      <label>Text Color:</label>
      <input
        type="color"
        value={textColor}
        onChange={(e) => handleTextColorChange()}
      />
      <br />
      <label>Font Size:</label>
      <input
        type="range"
        min="10"
        max="30"
        value={fontSize}
        onChange={(e) => handleFontSizeChange()}
      />
      <br />
      <label>Width :</label>
      <input type="text" value={width} onChange={(e) => handleWidthChange()} />
      <br />
      <DropdownMenu
        options={['Arial', 'Helvetica', 'Verdana']}
        handleFontFamilyChange={handleFontFamilyChange()}
      />
      <button onClick={saveSettingsToDatabase}>save settings</button>
    </div>
  );
};

function DropdownMenu({ options, handleFontFamilyChange }) {
  return (
    <div>
      <label>Select an option:</label>
      <select onChange={(e) => handleFontFamilyChange}>
        {options.map((option, index) => (
          <option key={index} value={option}>
            {option}
          </option>
        ))}
      </select>
    </div>
  );
}
export default PropertiesSection;
